  <template>
    <div>
      欢迎来到nodeJs后台管理<br>
      there is no class type of typescript!!!
      <button type="button" @click="msgShow">展示提示框</button>
      <button type="button" @click="routerGo">用户登录</button>

    </div>
  </template>

<script>
import Toast from '../../libs/toast/toast';
import test from './test.js'

export default {
  data(){
    return {
      data:'this is a class dom,but here is no class.'
    }
  },
  methods:{
    query(){
        const inner = test('Params');
        inner();
    //   console.log(this.data);
    },
    routerGo(){
        this.$router.push('/logins')
    },
    msgShow(){
        const str = '这是一个提示框测试';
        Toast(str)
    }
  },
  mounted (){
    var vm = this;
    vm.query();
  }
}
</script>



